"use client"

import { useState } from "react"
import { useAuth } from "@/lib/auth-context"
import { Save, User } from "lucide-react"

export default function AdminConfiguracoes() {
  const { user } = useAuth()
  const [formData, setFormData] = useState({
    nome: user?.nome || "",
    sobrenome: user?.sobrenome || "",
    celular: user?.celular || "",
    email: user?.email || "",
  })
  const [saved, setSaved] = useState(false)

  const handleSave = () => {
    // Salvamento mock - em um app real atualizaria os dados do usuário
    setSaved(true)
    setTimeout(() => setSaved(false), 3000)
  }

  return (
    <div className="p-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-light text-foreground">Configurações</h1>
        <p className="text-foreground/60">Gerencie suas informações administrativas</p>
      </div>

      {/* Profile Card */}
      <div className="max-w-2xl">
        <div className="bg-card border border-border rounded-xl overflow-hidden">
          {/* Card Header */}
          <div className="p-6 border-b border-border bg-muted/30">
            <div className="flex items-center gap-4">
              <div className="w-16 h-16 rounded-full bg-primary text-primary-foreground flex items-center justify-center">
                <User size={32} />
              </div>
              <div>
                <h2 className="text-xl font-medium text-foreground">
                  {formData.nome} {formData.sobrenome}
                </h2>
                <p className="text-foreground/60">Administrador</p>
              </div>
            </div>
          </div>

          {/* Form */}
          <div className="p-6 space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">Nome</label>
                <input
                  type="text"
                  value={formData.nome}
                  onChange={(e) => setFormData({ ...formData, nome: e.target.value })}
                  className="w-full px-4 py-3 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">Sobrenome</label>
                <input
                  type="text"
                  value={formData.sobrenome}
                  onChange={(e) => setFormData({ ...formData, sobrenome: e.target.value })}
                  className="w-full px-4 py-3 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                />
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Celular</label>
              <input
                type="tel"
                value={formData.celular}
                onChange={(e) => setFormData({ ...formData, celular: e.target.value })}
                className="w-full px-4 py-3 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                placeholder="(11) 99999-9999"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-foreground mb-2">E-mail</label>
              <input
                type="email"
                value={formData.email}
                onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                className="w-full px-4 py-3 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
              />
            </div>

            {/* Success Message */}
            {saved && (
              <div className="p-4 bg-green-50 border border-green-200 text-green-700 rounded-lg">
                Configurações salvas com sucesso!
              </div>
            )}
          </div>

          {/* Actions */}
          <div className="p-6 border-t border-border bg-muted/30">
            <button
              onClick={handleSave}
              className="flex items-center gap-2 bg-primary text-primary-foreground px-6 py-3 rounded-lg font-medium hover:bg-primary/90 transition-colors"
            >
              <Save size={18} />
              Salvar Alterações
            </button>
          </div>
        </div>

        {/* Info Box */}
        <div className="mt-6 p-4 bg-accent/10 border border-accent/30 rounded-lg">
          <p className="text-sm text-foreground/70">
            <strong>Nota:</strong> Este é um ambiente de demonstração. As alterações são salvas apenas localmente e
            serão perdidas ao recarregar a página.
          </p>
        </div>
      </div>
    </div>
  )
}
